import React, { useEffect, useState } from "react";
import AlarmRow from "./AlarmRow";
import AlarmRowEvent3 from "./AlarmRowEvent3";
import { getRoomInfo } from "@/api/deviceConfiguration";
import { ConfigProvider } from "antd";

export default function NotificationConfiguration({
  elderly_id,
  room_details,
}) {
  const [roomDetails, setRoomDetails] = useState(null);
  const [loading, setLoading] = useState(false);
  console.log(room_details);

  const room_id = room_details?._id || null;
  const uid = room_details?.device_no || null;
  const is_device_bind = room_details?.is_device_bind;
  console.log(uid);

  useEffect(() => {
    if (!elderly_id || !uid) return;
    setLoading(true);
    getRoomInfo({ elderly_id, room_id: uid })
      .then((res) => {
        if (res?.status) {
          setRoomDetails(res.data);
        }
      })
      .catch((err) => console.error(err))
      .finally(() => setLoading(false));
  }, [elderly_id, uid]);

  // destructure safely
  const { eventStatus = {}, settings = {}, device_no = "" } = roomDetails || {};

  return (
    <ConfigProvider
      theme={{
        token: {
          colorPrimary: "#514EB5",
          colorLinkActive: "#514EB5",
          colorLinkHover: "#514EB5",
          colorLink: "#514EB5",
        },
      }}
    >
      <div className="bg-white rounded-md ">
        <div className="flex items-center gap-2 mb-4">
          <h3 className="text-base font-medium text-nowrap">Alarm Settings</h3>
          <hr className="w-full m-0" />
        </div>

        {/* Loading state */}
        {loading && (
          <div className="text-center text-sm text-gray-500 py-4">
            Loading alarm settings
          </div>
        )}

        {/* Device offline */}
        {!loading && is_device_bind === false && (
          <div className="text-center text-sm text-red-500 py-4">
            The device is offline. Please bring it online to change alarm
            settings.
          </div>
        )}

        {/* Show alarms only if device is online and not loading */}
        {!loading && is_device_bind !== false && (
          <>
            {/* Fall Alarm */}
            <AlarmRow
              elderlyId={elderly_id}
              roomId={room_id}
              eventId="2"
              uid={device_no}
              label="Fall Alarm"
              active={eventStatus["2"] || false}
              fields={{
                time: settings?.time || 0, // notification time in sec
              }}
              type="fall"
            />

            {/* Combined Breath + Heart */}
            <AlarmRowEvent3
              elderlyId={elderly_id}
              roomId={room_id}
              uid={device_no}
              label="Breath & Heart Rate Alarm"
              active={eventStatus["3"] || false}
              fields={{
                lower_breath: settings?.lower_breath || 0,
                upper_breath: settings?.upper_breath || 0,
                lower_heartbeat: settings?.lower_heartbeat || 0,
                upper_heartbeat: settings?.upper_heartbeat || 0,
                sudden_duration: settings?.sudden_duration || 0,
                sensitivity: settings?.sensitivity || 0,
              }}
            />

            {/* Off Bed Alarm */}
            <AlarmRow
              elderlyId={elderly_id}
              roomId={room_id}
              eventId="6"
              uid={device_no}
              label="Off Bed Alarm"
              active={eventStatus["6"] || false}
              fields={{
                leave_detection_time: settings?.leave_detection_time || 0,
                leave_detection_range: settings?.leave_detection_range || "",
              }}
              type="offbed"
            />

            {/* Inactivity Alarm */}
            <AlarmRow
              elderlyId={elderly_id}
              roomId={room_id}
              eventId="4"
              uid={device_no}
              label="Inactivity Alarm"
              active={eventStatus["4"] || false}
              fields={{}}
              type="inactivity"
            />
          </>
        )}
      </div>
    </ConfigProvider>
  );
}
