import React, { useState, useRef } from "react";
import { Input, Switch, TimePicker } from "antd";
import dayjs from "dayjs";
import {
  updateFunctionParameters,
  updateFunctionStatus,
} from "@/api/deviceConfiguration";

export default function AlarmRow({
  elderlyId,
  roomId,
  eventId,
  uid,
  label,
  active,
  fields,
  type,
}) {
  const [isActive, setIsActive] = useState(active);

  const [formData, setFormData] = useState(() => {
    if (type === "fall") {
      return { ...fields, time: fields.time * 10 };
    } else if (type === "offbed") {
      // split the range string into start and end
      const [start, end] = fields.leave_detection_range
        ? fields.leave_detection_range.split("-")
        : ["", ""];
      return {
        ...fields,
        leave_detection_start: start,
        leave_detection_end: end,
      };
    } else {
      return fields;
    }
  });

  const prevFormData = useRef(formData);

  const handleToggle = async (checked) => {
    setIsActive(checked);
    await updateFunctionStatus({
      elderly_id: elderlyId,
      room_id: roomId,
      event_id: eventId,
      data: { status: checked },
    });
  };

  const handleFieldChange = (key, value) => {
    setFormData({ ...formData, [key]: value });
  };
  const handleSave = async (keyChanged) => {
    let payload = { uid, event: eventId };

    if (type === "fall") {
      if (JSON.stringify(prevFormData.current) === JSON.stringify(formData))
        return;
      payload.time = Number(formData.time) / 10;
    } else if (type === "offbed") {
      payload.leave_detection_time = Number(formData.leave_detection_time);
      payload.leave_detection_range = `${
        formData.leave_detection_start || "00:00"
      }-${formData.leave_detection_end || "23:59"}`;
    }

    try {
      await updateFunctionParameters({ elderly_id: elderlyId, data: payload });
      prevFormData.current = { ...formData }; // update previous value only on success
    } catch (err) {
      console.error(err);
      // revert only the field that changed to previous value
      setFormData((prev) => ({
        ...prev,
        [keyChanged]: prevFormData.current[keyChanged],
      }));
    }
  };

  return (
    <div className="border rounded-md p-4 mb-3 flex flex-col lg:flex-row lg:items-center lg:justify-between gap-3">
      <span className="font-medium shrink-0">{label}</span>

      <div className="flex flex-wrap gap-4 items-center">
        {type === "fall" && (
          <div className="flex items-center gap-1">
            <Input
              type="number"
              className="border rounded-md px-2 py-1 w-20 text-sm"
              value={formData.time}
              onChange={(e) => handleFieldChange("time", e.target.value)}
              onBlur={handleSave}
              suffix="sec"
            />
            <span className="text-sm text-gray-500">Notification time</span>
          </div>
        )}

        {type === "offbed" && (
          <div className="flex gap-2">
            <div className="flex items-center gap-1">
              <Input
                type="number"
                className="border rounded-md px-2 py-1 w-20 text-sm "
                value={formData.leave_detection_time}
                onChange={(e) =>
                  handleFieldChange("leave_detection_time", e.target.value)
                }
                suffix="min"
                onBlur={handleSave}
              />
              <span className="text-sm text-gray-500">Threshold </span>
            </div>

            <div className="flex items-center gap-1">
              <TimePicker
                className="w-20"
                value={
                  formData.leave_detection_start
                    ? dayjs(formData.leave_detection_start, "HH:mm")
                    : null
                }
                format="HH:mm"
                onChange={(time, timeString) =>
                  handleFieldChange("leave_detection_start", timeString)
                }
                onOk={() => handleSave("leave_detection_start")}
                placeholder="Start"
              />
              <TimePicker
                className="w-20"
                value={
                  formData.leave_detection_end
                    ? dayjs(formData.leave_detection_end, "HH:mm")
                    : null
                }
                format="HH:mm"
                onChange={(time, timeString) =>
                  handleFieldChange("leave_detection_end", timeString)
                }
                onOk={() => handleSave("leave_detection_end")}
                placeholder="End"
              />

              <span className="text-sm text-gray-500">Time Range</span>
            </div>
          </div>
        )}
      </div>
      {type !== "fall" && (
        <Switch
          size="small"
          className="w-10"
          checked={isActive}
          onChange={handleToggle}
        />
      )}
      {type === "fall" && <div></div>}
    </div>
  );
}
