import React, { useState, useRef } from "react";
import { Switch } from "antd";
import {
  updateFunctionParameters,
  updateFunctionStatus,
} from "@/api/deviceConfiguration";

export default function AlarmRowEvent3({
  elderlyId,
  roomId,
  uid,
  label,
  active,
  fields,
}) {
  const [isActive, setIsActive] = useState(active);
  const [formData, setFormData] = useState(fields);
  const prevFormData = useRef(fields); // store previous values

  const handleToggle = async (checked) => {
    setIsActive(checked);
    await updateFunctionStatus({
      elderly_id: elderlyId,
      room_id: roomId,
      event_id: "3",
      data: { status: checked },
    });
  };

  const handleFieldChange = (key, value) => {
    setFormData({ ...formData, [key]: value });
  };

  const handleSave = async (keyChanged) => {
    // check if value actually changed
    if (formData[keyChanged] === prevFormData.current[keyChanged]) return;

    const payload = {
      uid,
      event: "3",
      upper_breath: Number(formData.upper_breath),
      lower_breath: Number(formData.lower_breath),
      upper_heartbeat: Number(formData.upper_heartbeat),
      lower_heartbeat: Number(formData.lower_heartbeat),
      sudden_duration: Number(formData.sudden_duration),
      sensitivity: Number(formData.sensitivity),
    };

    try {
      await updateFunctionParameters({ elderly_id: elderlyId, data: payload });
      prevFormData.current = { ...formData }; // update previous on success
    } catch (err) {
      console.error(err);
      // revert only the field that failed
      setFormData((prev) => ({
        ...prev,
        [keyChanged]: prevFormData.current[keyChanged],
      }));
    }
  };

  const renderInput = (key, label, width = "w-20") => (
    <div className="flex items-center gap-1">
      <input
        type="number"
        className={`border rounded-md px-2 py-1 ${width} text-sm`}
        value={formData[key]}
        onChange={(e) => handleFieldChange(key, e.target.value)}
        onBlur={() => handleSave(key)}
      />
      <span className="text-sm text-gray-500">{label}</span>
    </div>
  );

  return (
    <div className="border rounded-md p-4 mb-3 flex flex-col gap-3">
      <div className="flex items-center justify-between">
        <span className="font-medium">{label}</span>
        <Switch
          size="small"
          className="w-10"
          checked={isActive}
          onChange={handleToggle}
        />
      </div>

      {/* Breath Rate */}
      <div className="flex flex-wrap gap-4 items-center">
        {renderInput("lower_breath", "Breath Min")}
        {renderInput("upper_breath", "Breath Max")}
      </div>

      {/* Heart Rate */}
      <div className="flex flex-wrap gap-4 items-center">
        {renderInput("lower_heartbeat", "Heart Min")}
        {renderInput("upper_heartbeat", "Heart Max")}
      </div>

      <hr className="border-gray-200 my-2 w-full" />

      {/* Sudden Duration + Sensitivity */}
      <div className="flex flex-wrap gap-4 items-center">
        {renderInput("sudden_duration", "Sudden Duration", "w-24")}
        {renderInput("sensitivity", "Sensitivity", "w-24")}
      </div>
    </div>
  );
}
